form {
  max-width: 81.25rem;
  margin: 0 auto;

  @media (max-width: 520px) {
    margin-left: 20px;
  }

  .form_first_section {
    width: 100%;
    height: auto;
    margin:40px 20px 0 40px;
    display: flex;
    align-items: baseline;

    @media (max-width: 520px) {
      display: flex;
      flex-direction: column;
      margin-right: 10px;
    }

    .form_service_option_empty {
      font-style: italic;
      font-size: 12px
    }
  }

  .form_service_choose {
    font-weight: bold;
    text-transform: uppercase;
    padding-right: 10px;

    @media (max-width: 520px) {
      padding-bottom: 10px;
    }
  }

  .form_restore_sec1 {
    @extend.form_first_section;
    margin-top: 20px;
    display: flex;

    .form_restore_1 {
      @extend.form_service_choose;
      font-weight: normal;
    }
  }

  .form_restore_sec3, .form_restore_sec4 {
    @extend.form_first_section;
    margin-top: 20px;

    .form_restore_3, .form_restore_4 {
      @extend.form_service_choose;
      font-weight: normal;

      @media (min-width: 350px) and (max-width: 520px) {
        width: 80%;
      }
    }
  }

  .form_restore_sec2 {
    display:flex;
    flex-direction: column;
    @extend.form_first_section;
    margin-top: 20px;

    .form_restore_2 {
      @extend.form_service_choose;
      font-weight: normal;
    }

    .form_restore_textarea {
      width: 50%;
      height: auto;
      font-style: italic;

      @media (min-width: 360px) and (max-width: 520px) {
        width: 80%;
      }
    }
  }

  .form_restore_option, .form_service_option {
    font-size: 0.75rem;
  }

  .form_restore_option_empty {
    font-style: italic;
    font-size: 0.75rem;
  }

  .form_search_sec1, .form_search_sec2 {
    display: flex;
    flex-direction: column;
    @extend .form_first_section;
    margin-top: 1.25rem;

    .form_search_1, .form_search_2 {
      @extend .form_service_choose;
      font-weight: normal;

      @media (min-width: 350px) and (max-width: 520px) {
        width: 80%;
      }
    }

    .form_search_textarea {
      width: 50%;
      height: auto;
      font-style: italic;

      @media (min-width: 360px) and (max-width: 520px) {
        width: 80%;
      }
    }
  }

  .form_search_sec3 {
    @extend .form_first_section;
    margin-top: 20px;

    .form_search_3 {
      @extend .form_service_choose;
      font-weight: normal;

      @media (min-width: 350px) and (max-width: 520px) {
        width: 80%;
      }

      .form_search_sel1 {
        width: 50%;
        height: auto;
        font-style: italic;

        @media (min-width: 360px) and (max-width: 520px) {
          width: 80%;
        }
      }
    }
  }
  .form_data_text {
    @extend .form_service_choose;
    margin:60px 20px 0 40px;
  }

  .form_data_sec1, .form_data_sec4 {
    width: 50%;
    height: auto;
    margin:20px 20px 0 40px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    text-transform: uppercase;

    @media (max-width: 520px) {
      display: flex;
      flex-direction: column;
      margin-right: 10px;
    }
  }

  .form_data_sec2, .form_data_sec3 {
    width: 50%;
    height: auto;
    margin:20px 20px 0 40px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    text-transform: uppercase;

    .form_p {
      text-transform: none;
    }
  }
}

.form_data_rodo {
  margin:40px 20px 0 40px;

  .form_rodo_text {
    font-size: 0.75rem;
    margin-bottom: 10px;
  }
}

.form_button_container {
  display:flex;
  justify-content: flex-end;
  margin:40px 240px 40px 40px;

  @media (min-width: 360px) and (max-width: 520px) {
    margin:40px 40px 40px 40px;
    justify-content: center;
  }

  .form_submit_button {
    text-transform: uppercase;
    padding: 20px;
    background-color: #dcdcdc;
    border-style: inherit;
  }

  .form_submit_button:hover {
    background-color: #008000
  }
}

.form_button_backicon {
  color:#A1A194;
  margin:40px 40px 10px 40px;
}

.form_search_option_empty {
  font-style: italic;
  font-size: 0.75rem;
  }

.form_search_option {
  font-size: 0.75rem;
  }

.form_data_input, .form_data_input_1, .form_data_input_2 {
   width: 100%;

  @media (min-width: 360px) and (max-width: 520px) {
    width: 140%;
    margin-bottom: 10px;
  }

}